
.container{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    flex-direction: column;
    background-image: linear-gradient(red, yellow, blue);
}
h1{
    margin-bottom: 30px;
    padding: 5px 10px;
    background-color:white;
    color: black;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.output{
    outline: none;
    border:none;
    width:320px;
    background: #333333;
    height: 70px;
    font-size: 32px;
    padding: 2px 10px;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
    color: #fff;
    text-align: right;

} 
button{
    width: 80px;
    height: 70px;
    font-size: 26px;
    cursor: pointer;
    border:none;
    border-top:1px solid  #666;
    border-right:1px solid  #666;
}